.profile_card {
    padding: 20px 40px 40px;
  max-width: 600px;
  &__image {
    width: 250px;
    margin: 30px 30px 30px 0;
    float: left;
  }
  &__text {
    display: inline;
  }
  }
@media screen and(max-width:800px) {
    .profile_card{
      padding: 20px 10px 10px;
    }
    
    &__text {
      display: inline;
    }
}  

  
  .va p{
    display: none;
    
  }
  .name{
    font-size: 20px;
  }
  #avatar{
    border-radius:50%;
  }
  @media (max-width:996px) {
    .va{
      align-items: left;
    }
    .va img{
      display: inline;
      vertical-align: top;
      margin-left: 10px;
    }
    .va p {
      display: inline-block;
      line-height: 28px;
      margin-right: 10px;
      font-size: 24px;
      margin-left: 20px;
    }
    .name{
      display: inline-block;
      line-height: 28px;
      margin-right: 10px;
      font-size: 24px;
      margin-left: 0px;
      right: 20px;
    }
    #avatar{
      height: 70px;
      width: 90px;
      margin-left: 40px;
    }
  }
  
  $white: #fff;
  $google-blue: #4285f4;
  $face-blue: #4267B2;
  $button-active-blue: #1669F2;
  
  .google-btn {
    width: 184px;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
    .google-icon-wrapper {
      position: absolute;
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-radius: 2px;
      background-color: $white;
    }
    .google-icon {
      position: absolute;
      margin-top: 11px;
      margin-left: 11px;
      width: 18px;
      height: 18px;
    }
    .btn-text {
      float: right;
      margin: 11px 11px 0 0;
      color: $white;
      font-size: 14px;
      letter-spacing: 0.2px;
      font-family: "Roboto";
    }
    &:hover {
      box-shadow: 0 0 6px $google-blue;
    }
    &:active {
      background: $button-active-blue;
    }
  }
  .fbutton{
    margin-left: 7%;
  }
  .face-btn {
    width: 200px;
    height: 42px;
    background-color: $face-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
    .Face-icon-wrapper {
      position: absolute;
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-radius: 2px;
      background-color: $white;
    }
    .face-icon {
      position: absolute;
      margin-top: 0px;
      margin-left: 0px;
      width: 40px;
      height: 40px;
    }
    .btn-text {
      float: right;
      margin: 11px 11px 0 0;
      color: $white;
      font-size: 14px;
      letter-spacing: 0.2px;
      font-family: "Roboto";
    }
    &:hover {
      box-shadow: 0 0 6px $face-blue;
    }
    &:active {
      background: $button-active-blue;
    }
  }
  .buttons{
    display: flex;
    align-items: center;
  }
  .gbutton{
      margin-left: 20%;
  }
  .separator{
      display:flex;
      align-items: center;
      margin-top: 2%;
      margin-bottom: 1%;
    }
    
  .separator .line{
      height: 1px;
      flex: 1;
      background-color: rgb(70, 66, 66);
  }
    
  .separator h6{
      padding: 0 2rem;
    }
  
  @import url(https://fonts.googleapis.com/css?family=Roboto:500);
  @media (max-width:996px) {
    .fbutton{
      margin-left: 4px;
    }  
    .gbutton{
      margin-left: 0px;
    }
  }
  @media (max-width:407px) {
    .google-btn{
      width:unset;
      .btn-text{
        font-size: unset;
      }
    }
  
    .face-btn{
      width: unset;
      .btn-text{
        font-size: unset;
      }
    }
  }  

