}

.profile{
  margin: 0;
  margin-left: 30%;
  margin-right: auto;
  left:0;
}

.profile_photo{
  border-radius: 40%;
  width:170px;
  height:150px;
  max-width: 100%;
  max-width:100%;
  display: block;
  border-radius: 40%;
  box-shadow: 0px 0px 5px #4195fc; 
}
.profile_photo:focus { 
  outline:none;
  border-color:#9ecaed;
  box-shadow:0 0 10px #9ecaed;
}
.profile_card{
  display: inline-block;
}
@media screen and (max-width: 800px) {
  .profile{
    margin: 0;
    margin-left: 50%;
    margin-right: 50%;
    left:0;
  }
  
  .profile_photo{
    border-radius: 40%;
    width:100px;
    height:100px;
    max-width: 100%;
    max-width:100%;
    display: block;
    border-radius: 40%;
    box-shadow: 0px 0px 5px #4195fc; 
  }
  .profile_photo:focus { 
    outline:none;
    border-color:#9ecaed;
    box-shadow:0 0 10px #9ecaed;
  }
  .profile_card{
    -webkit-align-items: center;
            align-items: center;
  }
  
}

